.slider {
  width: 270px;
  height: 389px;
  display: block;
  position: relative;
  .slide {
    position: absolute;
    width: 230px;
    height: 293px;
    // background: gray;
    z-index: 1;
    color: white;
    background-position: center;
    background-size: cover;
    &:nth-child(2n) {

    }
    &.active {
      z-index: 10;
      transform: translate(35px, 38px);
      transition: .3s ease;
    }
    &.active-prev {
      opacity: 0;
      z-index: 11;
      transform: translate(100px, 100px);
      transition: .3s ease;
    }
    &.active-next {
      z-index: 9;
      transition: .3s ease;
    }
  }
  .btn {
    cursor: pointer;
    position: absolute;
    outline: none;
    bottom: 31px;
    z-index:20;
    width: 22px;
    height: 8px;
    background-size: contain;
    background-repeat: no-repeat;
    &-next {
      right: 105px;
    }
    &-prev {
      left: 105px;
    }
  } 
}

.infoblock-slider {
  display: none;
}