.preloader {
  background: url('../img/loader_back.jpg') no-repeat center center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column wrap;
  transition: 1.2s ease;
  z-index: 2;
  &.hided {
    display: none;
  }
  img {
    position: relative;
    width: 100%;
    max-width: 150px;
  }
}

.main {
  display: none;
  font-family: $f-base;
  font-weight: 300;
  font-size: 16px;
  position: relative;
  &.loaded {
    display: block;
  }
  &-slider {
    @include from($vipad) {
      position: relative;
      overflow: hidden;
      top: 0;
    }
    .swiper-pagination {
      display: none;
      @include from($vipad) {
        display: block;
      }
      &-bullets {
        width: 25px;
        bottom: 30px !important;
        right: 30px !important;
        top: auto !important;
        transform: translate3d(0px, 0px, 0) !important;
        @include from($ipad) {
          bottom: 40px !important;
          right: 40px !important;
        }
        @include from($desktop) {
          bottom: 80px !important;
          right: 80px !important;
        }
      }
      &-bullet {
        font-size: 12px;
        font-weight: 500;
        width: 25px;
        height: 32px;
        position: relative;
        background: none !important;
        margin: 20px 0 !important;
        border-radius: 0;
        display: none;

        &.swiper-pagination-bullet-black {
          color: rgba(0, 0, 0, 0);
        }
        &.swiper-pagination-bullet-white {
          color: rgba(0, 0, 0, 0);
        }

        @include from($ipad) {
          font-size: 16px;
        }
        &:after {
          content: '';
          background: $c-black;
          width: 15px;
          height: 2px;
          position: absolute;
          top: 30px;
          right: 4px;
          @include from($ipad) {
            width: 20px;
            right: 2px;
          }
          @include from($laptop) {
            width: 25px;
          }
        }
        &-white {
          color: $c-white;
          &:after {
            background: $c-white;
          }
          &:before {
            position: absolute;
            left: -120px;
            top: -6px;
            text-align: right;
            width: 110px;
            @include from($ipad) {
              left: -135px;
            }
          }
          &:nth-child(2) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
              }
            }
          }

          &:nth-child(3) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -175px;
                  width: 150px;
                }
              }
            }
          }

          &:nth-child(4) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -165px;
                  width: 140px;
                }
              }
            }
          }

          &:nth-child(5) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -135px;
                }
              }
            }
          }

          &:nth-child(6) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -135px;
                }
              }
            }
          }

          &:nth-child(7) {
            &:hover {
              color: white;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '-';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  width: 130px;
                  left: -155px;
                }
              }
            }
          }
        }
        &-black {
          color: $c-black;
          &:after {
            background: $c-black;
          }
          &:before {
            position: absolute;
            left: -120px;
            top: -6px;
            text-align: right;
            width: 110px;
            @include from($ipad) {
              left: -135px;
            }
          }

          &:nth-child(2) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
              }
            }
          }

          &:nth-child(3) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -175px;
                  width: 150px;
                }
              }
            }
          }

          &:nth-child(4) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                content: ' ';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -165px;
                  width: 140px;
                }
              }
            }
          }

          &:nth-child(5) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -135px;
                }
              }
            }
          }

          &:nth-child(6) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  left: -135px;
                }
              }
            }
          }

          &:nth-child(7) {
            &:hover {
              color: $c-black;
              opacity: 1;
              .page-num {
                animation: showRight 1s 0.4s ease 1 both;
              }
              &:before {
                top: -1px;
                content: '-';
                animation: showRight 0.5s 0.1s ease 1 both;
                @include from($ipad) {
                  width: 130px;
                  left: -155px;
                }
              }
            }
          }
        }
        &-active {
          &:hover {
            .page-num {
              animation: none;
            }
          }
          .page-num {
            animation: none;
          }

          &.swiper-pagination-bullet-black {
            color: $c-black;
            &:hover {
              .page-num {
                animation: none;
              }
            }
          }
          &.swiper-pagination-bullet-white {
            color: $c-white;
            &:hover {
              .page-num {
                animation: none;
              }
            }
          }

          &:after {
            width: 20px;
            @include from($ipad) {
              width: 30px;
            }
            @include from($laptop) {
              width: 40px;
            }
          }
        }
      }
    }
    &.disabled {
      pointer-events: none;
    }

    &.swiper-container {
      height: 100%;
      @include from($vipad) {
        height: 100vh;
      }
    }
  }
}

