.header {
  color: $c-white;
  display: flex;
  position: relative;
  height: 100vh;
  overflow: hidden;
  @include from($vipad) {
    // min-height: 900px;
    height: 100vh;
  }
  @include from($ipad) {
    // min-height: 670px;
    height: 100vh;
  }
  @include from($laptop) {
    // min-height: 790px;
    height: 100vh;
  }
  @include from($desktop) {
    // min-height: 960px;
    height: 100vh;
  }
  &.fullheight {
    max-height: 625px;
    @include from($vipad) {
      max-height: none;
    }
  }
  // &-back {
  //   width: 100%;
  //   height: auto;
  //   height: 100%;
  //   position: absolute;
  //   background: url('../img/header.png') no-repeat center center;
  //   background-size: cover;
  // }

  &-video-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(
      17.12deg,
      #170202 13.01%,
      rgba(2, 6, 28, 0.63) 87.79%
    );
  }

  .video-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }

  // &-video,
  // &-canvas {
  //   position: absolute;
  //   top: 0;
  //   bottom: 0;
  //   right: 0;
  //   left: 0;
  //   margin: auto;
  //   min-height: 50%;
  //   min-width: 50%;
  // }

  .video-container > video {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  @media screen and (max-aspect-ratio: 1920/1080) {
    .video-container > video {
      height: 100%;
    }
  }
  @media screen and (min-aspect-ratio: 1920/1080) {
    .video-container > video {
      width: 100%;
    }
  }

  &-wrap {
    margin-bottom: 30px;
    display: flex;
    flex-flow: column wrap;
    @include from($vipad) {
      flex-flow: row nowrap;
    }
    @include from($ipad) {
      margin-bottom: 60px;
    }
    @include from($laptop) {
      margin-bottom: 78px;
    }
    @include from($desktop) {
      margin-bottom: 107px;
    }
  }
  &-top {
    display: flex;
    flex-flow: row wrap;
    align-items: center;

    margin: 30px 0 76px;
    @include from($vipad) {
      margin: 62px 0 365px;
    }
    @include from($ipad) {
      margin: 62px 0 144px;
    }
    @include from($laptop) {
      margin: 60px 0 163px;
    }
    @include from($desktop) {
      margin: 92px 0 197px;
    }
    img {
      width: 106px;
      margin-right: auto;
      @include from($vipad) {
        width: 171px;
        margin-right: 200px;
      }
      @include from($ipad) {
        margin-right: 389px;
      }
      @include from($laptop) {
        width: 201px;

        margin-right: 620px;
      }
      @include from($desktop) {
        width: 203px;
        margin-right: 958px;
      }
    }

    .lan {
      display: flex;
      flex-flow: row nowrap;
      text-transform: uppercase;
      position: absolute;
      bottom: 30px;
      left: 27px;
      @include from($vipad) {
        font-size: 10px;
        line-height: 28px;
        padding-top: 3px;
        position: static;
        margin-left: auto;
      }
      @include from($ipad) {
        padding-top: 0;
        font-size: 12px;
        line-height: 28px;
      }
      @include from($laptop) {
        font-size: 16px;
        line-height: 28px;
      }
      @include from($desktop) {
        font-size: 18px;
        line-height: 28px;
      }

      &-item {
        cursor: pointer;
        &:hover {
          opacity: 1;
        }
        opacity: 0.5;
        &.active {
          opacity: 1;
          cursor: initial;
        }
        &:nth-child(2) {
          cursor: initial;
          margin: 0 5px;
          opacity: 1;
        }

      }
    }

    &__phone {
      color: $c-white;
      font-weight: 500;
      font-size: 10px;
      line-height: 26px;
      letter-spacing: 0.1em;

      margin-left: auto;
      margin-right: 0;

      @include from($vipad) {
        font-size: 10px;
        line-height: 28px;
        margin-left: 33px;
        padding-top: 3px;
      }
      @include from($ipad) {
        padding-top: 0;
        font-size: 12px;
        line-height: 28px;
      }
      @include from($laptop) {
        font-size: 16px;
        line-height: 28px;
        margin-left: 49px;
      }
      @include from($desktop) {
        font-size: 18px;
        line-height: 28px;
        margin-left: 58px;
      }
    }
  }

  &-links {
    display: flex;
    position: absolute;
    bottom: 30px;
    right: 27px;
    li {
      margin-right: 15px;
      &:last-child {
        margin-right: 0;
      }
      @include from($vipad) {
        margin-right: 17px;
      }
      @include from($ipad) {
        margin-right: 19px;
      }
      @include from($laptop) {
        margin-right: 24px;
      }
      a {
        opacity: 0.8;
        display: block;
        width: 27px;
        height: 27px;
        background-position: center;
        background-size: cover;
        @include from($vipad) {
          width: 30px;
          height: 30px;
        }
        @include from($laptop) {
          width: 35px;
          height: 35px;
        }
        @include from($desktop) {
          width: 40px;
          height: 40px;
        }
        &:hover {
          opacity: 1;
        }
      }
    }
    @include from($vipad) {
      position: static;
      padding-top: 3px;
    }
    @include from($ipad) {
      padding-top: 0;
    }
  }

  &-info {
    margin-bottom: 33px;
    @include from($vipad) {
      width: 280px;
    }
    @include from($ipad) {
      width: 400px;
      // margin-right: 124px;
    }
    @include from($laptop) {
      width: 680px;
      // margin-right: 162px;
    }
    @include from($desktop) {
      width: 830px;
      // margin-right: 397px;
    }
    p {
      margin-bottom: 6px;
      @include from($vipad) {
        margin-bottom: 11px;
      }
      @include from($ipad) {
        margin-bottom: 20px;
      }
      @include from($laptop) {
        margin-bottom: 33px;
      }
    }
  }
  &-timer-wrap {
    @include from($vipad) {
      margin-left: auto;
    }
    .timer__label {
      display: flex;
      align-items: center;
      margin-bottom: 7px;
      @include from($vipad) {
        margin-bottom: 36px;
      }
      @include from($ipad) {
        margin-bottom: 31px;
      }
      @include from($laptop) {
        margin-bottom: 74px;
      }
      @include from($desktop) {
        margin-bottom: 56px;
      }
      p {
        @include from($ipad) {
          padding-left: 25px;
        }
        @include from($laptop) {
          padding-left: 0;
        }
      }
      span {
        margin-left: 10px;
        border-bottom: 1px solid $c-white;
        width: 157px;
        height: 1px;
        @include from($vipad) {
          width: 140px;
          margin-left: 15px;
        }
        @include from($ipad) {
          width: 207px;
          margin-left: 8px;
        }
        @include from($laptop) {
          width: 239px;
          margin-left: 24px;
        }
        @include from($desktop) {
          width: 398px;
          margin-left: 29px;
        }
      }
    }
    .timer__value {
      display: flex;
      flex-flow: row nowrap;
      margin-right: -25px;
      &--item {
        color: $c-white;
        font-weight: 600;
        position: relative;
        flex-flow: column;
        align-items: center;
        width: 73px;
        padding: 0 25px 0 0;
        @include from($vipad) {
          width: 73px;
          padding: 0 13px 0 0;
        }
        @include from($ipad) {
          width: 97px;
          padding: 0 20px 0 0;
        }
        @include from($laptop) {
          width: 113px;
          padding: 0 27px 0 0;
        }
        @include from($desktop) {
          width: 154px;
          padding: 0 35px 0 0;
        }

        &:first-child {
          &:after {
            @include from($ipad) {
              right: 12px;
            }
            // @include from($laptop) {
            //   right: -22px;
            // }
            @include from($desktop) {
              right: 20px;
            }
          }
        }
        &:after {
          content: ':';
          position: absolute;
          top: 6px;
          font-weight: 600;
          font-size: 20px;
          right: 9px;
          @include from($ipad) {
            right: 7px;
            font-weight: 500;
            font-size: 29px;
          }
          @include from($laptop) {
            font-size: 40px;
            top: 10px;
          }
          @include from($desktop) {
            font-size: 57px;
          }
        }
        &:last-child {
          &:after {
            display: none;
          }
        }
      }
      &__number {
        text-align: center;
        width: 100%;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        margin-bottom: 8px;
        @include from($ipad) {
          font-size: 36px;
          line-height: 44px;
          margin-bottom: 13px;
        }
        @include from($laptop) {
          font-size: 48px;
          line-height: 59px;
          margin-bottom: 15px;
        }
        @include from($desktop) {
          font-size: 72px;
          line-height: 88px;
          margin-bottom: 10px;
        }
      }
      &__label {
        text-align: center;
        width: 100%;
        font-weight: 600;
        font-size: 8px;
        line-height: 10px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        @include from($ipad) {
          font-weight: 500;
          font-size: 10px;
          line-height: 12px;
        }
        @include from($laptop) {
          font-size: 12px;
          line-height: 15px;
        }
        @include from($desktop) {
          font-size: 14px;
          line-height: 17px;
        }
      }
    }
  }
  &-actions {
    //
    .btn--with-icon, .btn--outline {
      visibility: hidden;
    }
    display: flex;
    flex-flow: column nowrap;
    @include from($vipad) {
      flex-flow: row nowrap;
      align-items: end;
    }
    &__main {
      position: relative;
      display: flex;
      flex-flow: row wrap;
      @include from($vipad) {
        width: 50%;
      }
      @include from($ipad) {
        flex-flow: row nowrap;
        align-items: center;
        width: auto;
      }
      p {
        width: 100%;
        font-weight: 500;
        font-size: 12px;
        line-height: 38px;
        @include from($vipad) {
          font-size: 14px;
          line-height: 38px;
        }
        @include from($desktop) {
          font-size: 18px;
          line-height: 38px;
        }
      }
    }
    .btn {
      &--primary {
        margin: 0 19px 10px 0;
        @include from($vipad) {
          margin: 0 13px 30px 0;
        }
        @include from($ipad) {
          margin: 0 44px 0 0;
          min-width: 233px;
        }
        @include from($laptop) {
          margin-right: 48px;
        }
        @include from($desktop) {
          margin-right: 58px;
          min-width: 300px;
        }
      }
      &--outline {
        margin-left: auto;
      }
    }
  }
  .arrow-down__wrap {
    display: none;
    @include from($vipad) {
      display: block;
      position: absolute;
      bottom: 29px;
      left: 49.1%;
      display: flex;
      flex-flow: column wrap;
      align-items: center;
    }
    @include from($ipad) {
      bottom: 39px;
      left: 49.3%;
    }
    @include from($laptop) {
      bottom: 84px;
      left: 49.2%;
    }
    @include from($desktop) {
      bottom: 131px;
      left: 49.4%;
    }
    .arrow-down {
      background: url(../img/arrow-down.svg);
      background-position: center;
      background-size: cover;
      width: 15px;
      height: 15px;

      @include from($laptop) {
        margin-top: 17px;
      }
    }
    .mouse-scroll {
      display: none;
      @include from($laptop) {
        display: block;
        background: url('../img/mouse.svg') no-repeat center center;
        width: 24px;
        height: 39px;
      }
      cursor: initial;
    }
  }
}

// Animated styles
//  ,       ,  . ,   = ,  .
//    -  -     .     ,    .

//   :
//                                  -           (          )
// animation: showFromBottom    .7s             .6s         ease      1                  both;

.header.animated {
  .lan {
    animation: showFromBottom 0.7s 0.6s ease 1 both;
  }
  .header-top {
    @include from($vipad) {
      animation: showFromBottom 0.7s 0.6s ease 1 both;
    }
  }
  .header-info {
    animation: showFromBottom 0.7s 0.75s ease 1 both;
  }
  .header-actions__main {
    animation: showFromBottom 0.6s 1s ease 1 both;
  }
  .header-timer-wrap {
    animation: showFromBottom 0.7s 1.25s ease 1 both;
  }
  .timer__label span {
    animation: showFromLeftToRight 0.9s 1.25s ease 1 both;
  }
  .header-actions .not-for-mobile {
    animation: showFromBottom 0.6s 1.5s ease 1 both;
  }
  .arrow-down__wrap {
    animation: showFromBottom 0.6s 2s ease 1 both;
  }
  .arrow-down {
    animation: showFromToptoBottom 0.9s 4s ease 3;
  }
}

@keyframes showFromToptoBottom {
  0% {
    transform: translate(0, 0);
    opacity: 1;
  }
  50% {
    transform: translate(0, 25px);
    opacity: 0.5;
  }

  100% {
    transform: translate(0, 0px);
    opacity: 0.2;
  }
}

@keyframes showFromLeftToRight {
  from {
    transform-origin: 0 0;
    transform: scaleX(0);
  }
  to {
    transform-origin: 0 0;
    transform: scaleX(1);
  }
}

@keyframes showFromBottom {
  from {
    transform: translate(0, 50px);
    opacity: 0;
  }
  to {
    transform: translate(0, 0);
    opacity: 1;
  }
}
