.btn {
  letter-spacing: 0.1em;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  &--primary {
    display: inline-block;
    color: $c-white;
    font-weight: bold;
    font-size: 8px;
    padding: 16px 40px;
    text-align: center;
    background: $c-red;

    &:hover {
      background: #af1743;
    }

    @include from($vipad) {
      padding: 12px 63px;
      font-size: 10px;
      line-height: 28px;
    }
    @include from($desktop) {
      padding: 19px 74px;
      font-size: 14px;
      line-height: 28px;
      min-width: 240px;
    }
  }
  &--outline {
    color: $c-white;
    font-weight: bold;
    font-size: 10px;
    line-height: 28px;
    border: 1px solid $c-white;
    padding: 11px 62px;

    &:hover {
      border: 1px solid $c-red;
    }
    @include from($desktop) {
      font-size: 14px;
      line-height: 28px;
      padding: 18px 62px;
    }
  }
  &--inline {
    color: $c-white;
    font-weight: 600;
    font-size: 10px;
    line-height: 28px;

    @include from($ipad) {
      font-weight: 500;
      font-size: 12px;
    }
    @include from($laptop) {
      font-size: 16px;
    }
    @include from($desktop) {
      font-size: 18px;
    }
  }

  &--underline {
    &:hover {
      text-decoration: underline;
    }
  }

  &--with-icon {
    width: 44px;
    height: 40px;
    border: 1px solid $c-white;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      border: 1px solid $c-red;
    }
  }
  &--brown {
    font-weight: bold;
    font-size: 8px;
    line-height: 26px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $c-red;
    &:hover {
      text-decoration: underline;
    }
    @include from($ipad) {
      font-size: 10px;
      line-height: 26px;
    }
    @include from($desktop) {
      font-size: 12px;
      line-height: 28px;
    }
  }
}
