.grid-container {
  margin: 0 auto;
  padding: 0 27px;
  position: relative;
  min-width: 320px;
  width: 100%;
  @include from($vipad) {
    padding: 0 64px;
    max-width: 768px;
  }
  @include from($ipad) {
    padding: 0 85px;
    max-width: 1024px;
  }
  @include from($laptop) {
    padding: 0 120px;
    max-width: 1440px;
  }
  @include from($desktop) {
    padding: 0 160px;
    max-width: 1920px;
  }
}

.space-size {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 31px;
  @include from($vipad) {
    margin-bottom: 42px;
  }
  @include from($ipad) {
    margin-bottom: 31px;
  }
  @include from($laptop) {
    margin-bottom: 42px;
  }
  @include from($desktop) {
    margin-bottom: 52px;
  }
  &__item {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 56px;
    padding-right: 56px;
    box-sizing: content-box;
    &:last-child {
      padding-right: 0;
    }
    &__title {
      font-weight: 600;
      font-size: 36px;
      margin-bottom: 10px;
      @include from($ipad) {
        font-size: 48px;
        margin-bottom: 12px;
      }
      @include from($laptop) {
        font-size: 72px;
        margin-bottom: 17px;
      }
      @include from($desktop) {
        font-size: 90px;
      }
    }
    &__label {
      font-weight: 600;
      font-size: 8px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      text-align: center;
      line-height: 1.4em;
      @include from($ipad) {
        font-weight: 500;
        font-size: 10px;
      }
      @include from($laptop) {
        font-size: 14px;
      }
    }
    @include from($vipad) {
      width: 60px;
      padding-right: 60px;
    }
    @include from($ipad) {
      width: 70px;
      padding-right: 90px;
    }
    @include from($laptop) {
      width: 90px;
      padding-right: 122px;
    }
    @include from($desktop) {
      width: 121px;
      padding-right: 130px;
    }
  }
  &__line {
    position: absolute;
    border-bottom: 1px solid $c-black;
    width: 37px;
    top: 18px;
    left: 65px;

    &--white {
      border-bottom: 1px solid $c-white;
    }

    @include from($vipad) {
      width: 45px;
      left: 67px;
    }
    @include from($ipad) {
      width: 60px;
      top: 24px;
      left: 85px;
    }
    @include from($laptop) {
      width: 80px;
      top: 36px;
      left: 110px;
    }
    @include from($desktop) {
      width: 100px;
      top: 45px;
      left: 135px;
    }
  }
}

.infoblock {
  &-wrap {
    overflow: hidden;
    margin-top: 80px;
    @include from($vipad) {
      margin-top: 0;
    }
    // @include from($laptop) {
    //   margin-top: 132px;
    // }
    // @include from($desktop) {
    //   margin-top: 100px;
    // }
    p {
      line-height: 24px;
      padding-right: 10px;
      margin-bottom: 50px;
      @include from($vipad) {
        font-size: 12px;
        margin-right: -35px;
        padding-right: 10px;
      }
      @include from($ipad) {
        font-size: 14px;
        line-height: 25px;
        padding-right: 0;
        margin-right: 0;
        margin-bottom: 60px;
      }
      @include from($laptop) {
        font-size: 18px;
        line-height: 34px;
        margin-bottom: 79px;
      }
      @include from($desktop) {
        margin-bottom: 88px;
      }
    }

    &__info {
      .section-title {
        margin-bottom: 30px;
        @include from($vipad) {
          margin-top: 10px;
          margin-bottom: 36px;
        }
        @include from($ipad) {
          margin-top: 41px;
        }
        @include from($laptop) {
          margin-top: 57px;
          .h2 {
            line-height: 61px;
          }
        }
        @include from($desktop) {
          margin-top: 82px;
          margin-bottom: 56px;
          .h2 {
            line-height: 82px;
          }
        }
      }
      .not-for-mobile {
        display: none;
        @include from($vipad) {
          display: inline-block;
        }
      }

      @include from($vipad) {
        width: 50%;
        padding: 0 43px;
        margin-right: 33px;
      }
      @include from($ipad) {
        padding-left: 86px;
        padding-right: 0;
        margin-right: 45px;
      }
      @include from($laptop) {
        padding-left: 120px;
        margin-right: 60px;
      }
      @include from($desktop) {
        padding-left: 160px;
        margin-right: 85px;
        padding-right: 70px;
      }
    }
    &.default {
      @include from($vipad) {
        display: flex;
        flex-flow: row nowrap;
      }
      .slider {
        @include from($vipad) {
          margin-top: -20px;
          width: 230px;
          height: 338px;
        }
        @include from($ipad) {
          margin-top: 0;
          width: 350px;
          height: 442px;
        }

        @include from($laptop) {
          width: 470px;
          height: 600px;
        }
        @include from($desktop) {
          width: 620px;
          height: 769px;
        }
        .slide {
          @include from($vipad) {
            width: 192px;
            height: 240px;
            &.active {
              transform: translate(30px, 30px);
            }
          }
          @include from($ipad) {
            width: 256px;
            height: 320px;
            &.active {
              transform: translate(40px, 40px);
            }
          }
          @include from($laptop) {
            width: 360px;
            height: 450px;
            &.active {
              transform: translate(60px, 60px);
            }
          }
          @include from($desktop) {
            width: 480px;
            height: 600px;
            &.active {
              transform: translate(80px, 80px);
            }
          }
        }
        .btn {
          &:hover {
            svg path {
              stroke: $c-red;
            }
          }
          @include from($vipad) {
            &-next {
              right: 102px;
            }
            &-prev {
              left: 66px;
            }
          }
          @include from($ipad) {
            width: 30px;
            height: 10px;
            &-next {
              right: 174px;
            }
            &-prev {
              left: 78px;
            }
          }
          @include from($laptop) {
            &-next {
              right: 254px;
            }
            &-prev {
              left: 118px;
            }
          }
          @include from($desktop) {
            bottom: 18px;
            &-next {
              right: 380px;
            }
            &-prev {
              left: 141px;
            }
          }
        }
      }
    }

    &.reverse {
      // margin-top: 94px;
      display: flex;
      flex-flow: column wrap;
      justify-content: flex-end;

      @include from($vipad) {
        flex-flow: row-reverse nowrap;
        justify-content: flex-end;
      }
      // @include from($ipad) {
      //   margin-top: 172px;
      // }
      // @include from($laptop) {
      //   margin-top: 268px;
      // }
      // @include from($desktop) {
      //   margin-top: 191px;
