        &__info {
          @include from($vipad) {
            margin: 0;
            padding: 0;
            margin-top: 54px;
            padding: 0 40px;
          }
          @include from($ipad) {
            margin-top: 0;
            margin-left: 43px;
          }
          @include from($laptop) {
            margin-left: 200px;
          }
          @include from($desktop) {
            margin-left: 275px;
          }

          p {
            @include from($vipad) {
              padding-right: 20px;
            }
            @include from($ipad) {
              padding-right: 0;
            }
            @include from($laptop) {
              padding-right: 80px;
            }
            @include from($desktop) {
              margin-bottom: 96px;
            }
          }
        }
      }
      .slider {
        @include from($vipad) {
          padding-left: 35px;
          padding-top: 35px;
          width: 279px;
          height: 373px;
        }
        @include from($ipad) {
          padding-left: 45px;
          padding-top: 0px;
          width: 345px;
          height: 443px;
        }
        @include from($laptop) {
          padding-left: 60px;
          width: 560px;
          height: 598px;
        }
        @include from($desktop) {
          padding-left: 80px;
          width: 776px;
          height: 770px;
        }
        .slide {
          @include from($vipad) {
            width: 192px;
            height: 240px;
            &.active {
              transform: translate(30px, 30px);
            }
          }
          @include from($ipad) {
            width: 256px;
            height: 320px;
            &.active {
              transform: translate(40px, 40px);
            }
          }
          @include from($laptop) {
            width: 360px;
            height: 450px;
            &.active {
              transform: translate(60px, 60px);
            }
          }
          @include from($desktop) {
            width: 480px;
            height: 600px;
            &.active {
              transform: translate(80px, 80px);
            }
          }
        }
        .btn {
          &:hover {
            svg path {
