            }
          }
          @include from($vipad) {
            &-next {
              right: 51px;
            }
            &-prev {
              left: 162px;
            }
          }
          @include from($ipad) {
            width: 30px;
            height: 10px;
            &-next {
              right: 36px;
            }
            &-prev {
              left: 212px;
            }
          }
          @include from($laptop) {
            &-next {
              right: 56px;
            }
            &-prev {
              left: 328px;
            }
          }
          @include from($desktop) {
            bottom: 18px;
            &-next {
              right: 61px;
            }
            &-prev {
              left: 490px;
            }
          }
        }
      }
    }
    &.column {
      // margin-top: 94px;
      // @include from($vipad) {
      //   margin-top: 128px;
      // }
      // @include from($ipad) {
      //   margin-top: 177px;
      // }
      // @include from($laptop) {
      //   margin-top: 222px;
      // }
      // @include from($desktop) {
      //   margin-top: 191px;
      // }
      // &--robot {
      //   @include from($vipad) {
      //     margin-top: 85px;
      //   }
      //   @include from($ipad) {
      //     margin-top: 121px;
      //   }
      // }

      .infoblock-wrap {
        &__info {
          @include from($vipad) {
            width: 100%;
            padding: 0 64px;
            margin: 0;
          }
          @include from($ipad) {
            padding: 0 86px;
          }
          @include from($laptop) {
            padding: 0 120px;
          }
          @include from($desktop) {
            padding: 0 160px;
          }
          .section-title {
            @include from($vipad) {
              margin-top: 0;
              margin-bottom: 31px;
            }
            @include from($ipad) {
              margin-bottom: 37px;
            }
            @include from($laptop) {
              margin-bottom: 49px;
            }
          }
          .space-size__wrap {
            @include from($vipad) {
              display: flex;
              flex-flow: row-reverse nowrap;
            }
          }
          .btn--primary {
            @include from($vipad) {
              z-index: 12;
              position: absolute;
              bottom: 30px;
              right: 157px;
            }
            @include from($ipad) {
              bottom: 40px;
              right: 210px;
            }
            @include from($laptop) {
              bottom: 60px;
