            // background: url('../img/rigth-arrow-white.svg') no-repeat center;
            background-size: cover;
            width: 22px;
            height: 8px;
            @include from($vipad) {
              top: 165px;
              left: 70px;
            }
            @include from($ipad) {
              width: 34px;
              height: 12px;
              top: 219px;
              left: 98px;
            }
            @include from($laptop) {
              top: 303px;
              left: 118px;
            }
            @include from($desktop) {
