            background-size: cover;
            width: 22px;
            height: 8px;
            @include from($vipad) {
              top: 165px;
              left: 28px;
            }
            @include from($ipad) {
              width: 34px;
              height: 12px;
              top: 219px;
              left: 37px;
            }
            @include from($laptop) {
              top: 303px;
              left: 58px;
            }
            @include from($desktop) {
              top: 400px;
              left: 65px;
            }
          }
        }
      }
    }
    &.column--robot {
      .space-size__wrap {
        .btn--primary {
          visibility: hidden;
        }
      }
      .infoblock-slider {
        visibility: hidden;
      }
      // .infoblock-wrap__info {
      //   @include from($vipad) {
