}

//animations infoblock
.slider__item.animated {
  .territory-info__desc {
    animation: showFromBottom 0.5s 0.5s ease 1 both;
  }
  .btn--outline {
    animation: showFromBottom 0.5s 0.6s ease 1 both;
  }

  .territory,
  .infoblock-wrap.default,
  .infoblock-wrap.reverse {
    .txt.for-mobile,
    .btn--with-icon.for-mobile {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }
    .btn--primary.for-mobile {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }

    .section-title {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }
    .infoblock-wrap__info {
      animation: showFromBottom 0.7s 0.3s ease 1 both;
    }
    .slider {
      animation: showFromBottom 0.7s 0.5s ease 1 both;
    }
    .space-size__line {
      animation: leftToRightLine 0.5s 0.5s ease 1 both;
    }
    .space-size {
      animation: showFromBottom 0.7s 0.3s ease 1 both;
    }
    // .slide.active {
    //   animation: show .9s .5s ease 1 both;
    // }
    .territory-map {
      animation: showFromBottom 0.5s 0.3s ease 1 both;
      #Points {
        g {
          opacity: 0;
          &:nth-child(1) {
            animation: showPoints 0.5s 0.6s ease 1 both;
          }
          &:nth-child(2) {
            animation: showPoints 0.5s 0.7s ease 1 both;
          }
          &:nth-child(3) {
            animation: showPoints 0.5s 0.8s ease 1 both;
          }
          &:nth-child(4) {
            animation: showPoints 0.5s 0.9s ease 1 both;
          }
          &:nth-child(5) {
            animation: showPoints 0.5s 1s ease 1 both;
          }
          &:nth-child(6) {
            animation: showPoints 0.5s 1.1s ease 1 both;
          }
          &:nth-child(7) {
            animation: showPoints 0.5s 1.2s ease 1 both;
          }
          &:nth-child(8) {
            animation: showPoints 0.5s 1.3s ease 1 both;
          }
          &:nth-child(9) {
            animation: showPoints 0.5s 1.4s ease 1 both;
          }
          &:nth-child(10) {
            animation: showPoints 0.5s 1.5s ease 1 both;
          }
          &:nth-child(11) {
            animation: showPoints 0.5s 1.6s ease 1 both;
          }
          &:nth-child(12) {
            animation: showPoints 0.5s 1.7s ease 1 both;
          }
        }
      }
    }
  }

  .infoblock-wrap.column {
    .txt.for-mobile,
    .btn--with-icon.for-mobile {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }
    .slider {
      animation: showFromBottom 0.7s 0.5s ease 1 both;
    }
    .btn--primary.for-mobile {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }
    .section-title {
      animation: showFromBottom 0.5s 0.5s ease 1 both;
    }
    .space-size {
      animation: showFromBottom 0.5s 0.35s ease 1 both;
      .space-size__line {
        animation: leftToRightLine 0.5s 0.5s ease 1 both;
      }
    }
    .txt.not-for-mobile {
      animation: showFromBottom 0.5s 0.25s ease 1 both;
    }
    .infoblock-slider {
      animation: showFromBottom 0.5s 0.6s ease 1 both;
    }
    // .space-size__wrap {
    // .btn--primary {
    //   animation: showFromBottom 0.5s 0.6s ease 1 both;
    // }
    // }
  }
}

.sommelier-robot-slider {
  animation: showFromBottom 0.5s 0.5s ease 1 both;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes showPoints {
  0% {
    opacity: 0;
    r: 10;
  }

  100% {
    opacity: 1;
    r: 20;
  }
}

@keyframes showRight {
  from {
    transform: translate(30px, 0);
    opacity: 0;
  }
  to {
    transform: translate(0, 0);
    opacity: 1;
  }
}

@keyframes leftToRightLine {
  from {
    opacity: 0;
    width: 0px;
  }
  to {
    opacity: 1;
    @include from($vipad) {
      width: 45px;
    }
    @include from($ipad) {
      width: 60px;
    }
    @include from($laptop) {
      width: 80px;
    }
    @include from($desktop) {
      width: 100px;
    }
  }
}
