.footer {
  .h3 {
    font-weight: bold;
    font-size: 22px;
    line-height: 34px;
    @include from($vipad) {
      font-size: 24px;
      line-height: 34px;
    }
    @include from($ipad) {
      font-size: 28px;
      line-height: 34px;
    }
    @include from($laptop) {
      font-size: 36px;
      line-height: 48px;
    }
    @include from($desktop) {
      font-size: 42px;
      line-height: 48px;
    }
  }
  .txt {
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
    @include from($ipad) {
      font-size: 14px;
      line-height: 26px;
    }
    @include from($desktop) {
      font-size: 16px;
      line-height: 30px;
    }
  }

  &-wrap-top {
    margin-top: 81px;
    display: flex;
    flex-flow: column nowrap;
    @include from($vipad) {
      flex-flow: row nowrap;
      margin-top: 77px;
      margin-bottom: 5px;
    }
    @include from($ipad) {
      margin-top: 115px;
      margin-bottom: 10px;
    }
    @include from($laptop) {
      margin-top: 160px;
      justify-content: space-between;
      margin-bottom: 66px;
    }
    @include from($desktop) {
      margin-top: 180px;
    }
  }

  &-wrap-bottom {
    display: flex;
    flex-flow: column nowrap;
    @include from($vipad) {
      flex-flow: row-reverse;
      justify-content: flex-end;
      margin-bottom: 60px;
    }
    @include from($ipad) {
      margin-bottom: 90px;
    }
    @include from($laptop) {
      justify-content: space-between;
      margin-bottom: 100px;
    }
    @include from($desktop) {
      margin-bottom: 115px;
    }
  }

  &-top {
    .h3 {
      margin-bottom: 10px;
      @include from($vipad) {
        margin-bottom: 12px;
      }
      @include from($ipad) {
        margin-bottom: 23px;
      }
      @include from($laptop) {
        margin-bottom: 42px;
      }
    }
    .txt {
      margin-bottom: 32px;
      @include from($ipad) {
        padding-right: 196px;
      }
      @include from($laptop) {
        padding-right: 0;
      }
    }
    @include from($vipad) {
      margin-right: 97px;
    }
    @include from($ipad) {
      width: 400px;
      margin-right: 245px;
    }
    @include from($laptop) {
      margin-right: 0;
      width: 400px;
    }
    @include from($desktop) {
      width: 460px;
    }
  }

  &-mid {
    @include from($vipad) {
      margin-left: auto;
      width: 200px;
    }
    &__item {
      &:first-child {
        .h3 {
          margin-bottom: 10px;
          @include from($vipad) {
            margin-bottom: 12px;
          }
          @include from($ipad) {
            margin-bottom: 30px;
          }
          @include from($laptop) {
            margin-bottom: 42px;
          }
        }
        .txt {
          margin-bottom: 12px;
        }
        .btn {
          display: block;
          margin-bottom: 32px;
          @include from($vipad) {
            margin-bottom: 37px;
          }
          @include from($ipad) {
            margin-bottom: 40px;
          }
          @include from($laptop) {
            position: absolute;
            top: 246px;
          }
          @include from($desktop) {
            position: absolute;
            top: 290px;
          }
        }
        @include from($laptop) {
          width: 227px;
          margin-right: 35px;
        }
        @include from($desktop) {
          margin-right: 157px;
        }
      }
      &:last-child {
        .h3 {
          margin-bottom: 10px;
          @include from($ipad) {
            margin-bottom: 30px;
          }
          @include from($laptop) {
            margin-bottom: 42px;
          }
        }
        .btn {
          display: block;
          margin-bottom: 32px;
        }
      }
    }
    @include from($ipad) {
      width: 225px;
    }
    @include from($laptop) {
      display: flex;
      width: 483px;
    }
    @include from($desktop) {
      width: 620px;
    }
  }

  &-bottom {
    @include from($vipad) {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
    }
    &__line {
      display: block;
      width: 26px;
      height: 1px;
      margin: 13px 0px;
      border: none;
      background: $c-black;
      @include from($vipad) {
        transform: rotate(90deg);
        width: 22px;
      }
      @include from($ipad) {
        margin: 0 12px;
      }
    }
    .txt {
      &:last-child {
        display: block;
        margin-bottom: 40px;
        @include from($vipad) {
          margin: 0;
        }
      }
      @include from($vipad) {
        font-size: 8px;
      }
      @include from($ipad) {
        font-size: 14px;
      }
      @include from($desktop) {
        font-size: 16px;
      }
    }
  }

  &-socials {
    display: flex;
    font-weight: bold;
    font-size: 8px;
    line-height: 26px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    margin-top: 12px;
    margin-bottom: 27px;

    visibility: hidden;
    li {
      margin-right: 20px;
      @include from($vipad) {
        margin-right: 25px;
      }
      @include from($ipad) {
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
      }
      @include from($laptop) {
        margin-right: 33px;
      }
    }
    @include from($vipad) {
      margin: 0;
      margin-left: 73px;
    }
    @include from($ipad) {
      margin-left: 78px;
      font-size: 10px;
    }
    @include from($laptop) {
      font-size: 12px;
      line-height: 28px;
    }
  }
}
