.video {
  margin-top: 96px;
  @include from($vipad) {
    margin-top: 108px;
  }
  @include from($ipad) {
    margin-top: 166px;
  }
  @include from($laptop) {
    margin-top: 202px;
  }
  @include from($desktop) {
    margin-top: 155px;
  }
  .section-title {
    text-align: center;
    margin-bottom: 30px;
    @include from($vipad) {
      margin-bottom: 40px;
    }
    @include from($ipad) {
      margin-bottom: 45px;
    }
    @include from($laptop) {
      margin-bottom: 65px;
    }
    @include from($desktop) {
      margin-bottom: 90px;
    }
  }
  video {
    position: relative;
    width: 100%;
    height: 190px;
    @include from($vipad) {
      padding: 0 65px;
      height: 256px;
    }
    @include from($ipad) {
      padding: 0 85px;
      height: 341px;
    }
    @include from($laptop) {
      padding: 0 120px;
      height: 480px;
    }
    @include from($desktop) {
      padding: 0 150px;
      height: 640px;
    }
  }
  .btn {
    &--play {
      width: 40px;
      height: 40px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      background: url('../img/play.svg') no-repeat center;
      background-size: cover;
      @include from($vipad) {
        // top: 55%;
        width: 48px;
        height: 48px;
      }
      @include from($laptop) {
        // top: 60%;
        width: 81px;
        height: 81px;
      }
    }
  }
}

.video.animated {
  .section-title {
    animation: showFromBottom 0.5s 0.1s ease 1 both;
  }
  .video-wrap {
    animation: showFromBottom .5s .125s ease 1 both;
  }
}

