.tariffs {
  display: none;

  overflow: hidden;
  margin-top: 80px;
  @include from($vipad) {
    margin-top: 104px;
  }
  @include from($ipad) {
    margin-top: 98px;
  }
  @include from($laptop) {
    margin-top: 118px;
  }
  @include from($desktop) {
    margin-top: 193px;
  }
  .section-title {
    text-align: center;
    margin-bottom: 33px;
    @include from($vipad) {
      margin-bottom: 29px;
    }
    @include from($ipad) {
      margin-bottom: 40px;
    }
    @include from($laptop) {
      margin-bottom: 47px;
    }
    @include from($desktop) {
      margin-bottom: 55px;
    }
  }
  &-slider {
    height: 100%;
    .swiper-pagination {
      @include from($vipad) {
        display: none;
      }
      &-bullet-active {
        background: $c-black;
      }
    }
    &.swiper-container {
      @include from($vipad) {
        overflow: visible;
      }
    }

    .slider__wrap {
      height: 425px;
      @include from($vipad) {
        height: 530px;
        display: flex;
        align-items: center;
      }
      @include from($ipad) {
        height: 600px;
      }
      @include from($laptop) {
        height: 816px;
      }
      @include from($desktop) {
        height: 816px;
      }
    }
    .swiper-button {
      &-next {
        display: none;
        @include from($vipad) {
          display: block;
          &:after {
            display: none;
          }
          // background: url('../img/right-circle-arrow-black.svg') no-repeat
          // center;
          // background-size: cover;
          width: 50px;
          height: 50px;
          right: -24px;
          background-color: white;
          svg {
            width: 100%;
          }
          &:hover {
            svg circle {
              stroke: $c-red;
            }
          }
        }
        &.swiper-button-disabled {
          background-color: white;
          opacity: 1;
        }
      }
      &-prev {
        display: none;
        @include from($vipad) {
          display: block;
          &:after {
            display: none;
          }
          // background: url('../img/left-circle-arrow-black.svg') no-repeat center;
          // background-size: cover;
          width: 50px;
          height: 50px;
          left: -24px;
          background-color: white;
          svg {
            width: 100%;
          }
          &:hover {
            svg circle {
              stroke: $c-red;
            }
          }
        }
        &.swiper-button-disabled {
          background-color: white;
          opacity: 1;
        }
      }
    }
    .slider__item {
      width: 266px;
      height: 384px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      padding: 51px 30px 38px 30px;
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-between;
      @include from($vipad) {
        width: 320px;
        height: 487px;
        padding: 81px 37px 47px 37px;
      }
      @include from($ipad) {
        height: 548px;
        padding: 103px 49px 70px 49px;
      }
      @include from($laptop) {
        height: 650px;
        padding: 126px 57px 49px 59px;
      }
      @include from($desktop) {
        height: 742px;
        padding: 135px 88px 60px 88px;
      }
      &--best {
        border: 1px solid $c-red;
        &:after {
          position: absolute;
          content: ' ';
          letter-spacing: 0.05em;
          text-transform: uppercase;
          font-weight: bold;
          font-size: 8px;
          color: $c-white;
          background: linear-gradient(
            264.5deg,
            #de0040 -7.59%,
            #af1743 105.88%
          );
          text-align: center;
          line-height: 30px;
          border-radius: 20px;
          top: 11px;
          left: 29px;
          width: 120px;
          height: 30px;
          @include from($vipad) {
            top: 32px;
            left: 34px;
          }
          @include from($ipad) {
            width: 170px;
            height: 40px;
            line-height: 40px;
            top: 37px;
            left: 46px;
            font-size: 12px;
          }
          @include from($laptop) {
            top: 47px;
            left: 56px;
          }
          @include from($desktop) {
            top: 66px;
            left: 94px;
          }
        }
        @include from($vipad) {
          width: 353px;
          height: 507px;
          border: 3px solid $c-red;
          padding: 88px 37px 57px 35px;
        }
        @include from($ipad) {
          height: 568px;
          padding: 110px 45px 78px 45px;
        }
        @include from($laptop) {
          height: 670px;
          padding: 116px 56px 56px 56px;
        }
        @include from($desktop) {
          height: 762px;
          padding: 144px 95px 68px 95px;
        }
      }
      .h3 {
        font-weight: bold;
        font-size: 16px;
        margin-bottom: 13px;
        @include from($vipad) {
          font-size: 18px;
          margin-bottom: -7px;
        }
        @include from($ipad) {
          font-size: 24px;
          margin-bottom: 15px;
        }
        @include from($laptop) {
          font-size: 36px;
          margin-bottom: -53px;
        }
        @include from($desktop) {
        }
      }
      .txt {
        line-height: 24px;
        margin-bottom: 43px;
        @include from($vipad) {
          font-size: 12px;
        }
        @include from($ipad) {
          font-size: 14px;
          line-height: 25px;
        }
        @include from($laptop) {
          font-size: 14px;
          line-height: 28px;
        }
        @include from($desktop) {
          font-size: 18px;
          line-height: 34px;
        }
      }
      &__bottom {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        @include from($vipad) {
          flex-flow: column nowrap;
          align-items: flex-start;
        }
        .price {
          font-weight: 600;
          font-size: 24px;
          @include from($vipad) {
            margin-bottom: 28px;
          }
          @include from($ipad) {
            font-size: 36px;
          }
          @include from($laptop) {
            font-size: 48px;
          }
          @include from($desktop) {
          }
        }
        .btn {
          &--buy {
            background: $c-red;
            width: 70px;
            height: 52px;
            margin-left: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            @include from($vipad) {
              display: none;
            }
          }

          &--primary {
            display: none;
            @include from($vipad) {
              display: block;
            }
          }
        }
      }
    }
  }
}
