.map {
  position: relative;
  overflow: hidden;
  background: $c-black;
  margin-top: 65px;
  height: 537px;
  @include from($vipad) {
    margin-top: 85px;
    height: 280px;
  }
  @include from($ipad) {
    margin-top: 81px;
    height: 373px;
  }
  @include from($laptop) {
    margin-top: 122px;
    height: 525px;
  }
  @include from($desktop) {
    margin-top: 162px;
    height: 700px;
  }
  &-back {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include from($vipad) {
      width: 177%;
      background-position: -217px -201px;
    }
    @include from($ipad) {
      width: 100%;
      background-position: 0px 0px;
    }
  }
  &-wrap {
    position: relative;
    padding-top: 311px;
    padding-bottom: 20px;
    @include from($vipad) {
      padding-top: 150px;
      padding-bottom: 26px;
    }
    @include from($ipad) {
      padding-top: 237px;
      padding-bottom: 26px;
    }
    @include from($laptop) {
      padding-top: 363px;
    }
    @include from($desktop) {
      padding-top: 465px;
      padding-bottom: 49px;
    }

    .logo {
      width: 133px;
      @include from($laptop) {
        width: 156px;
      }
      @include from($desktop) {
        width: 229px;
      }
    }
  }
  &-bottom {
    position: relative;
    .txt-sm {
      font-weight: 500;
      font-size: 10px;
      line-height: 20px;
      &:first-child {
        margin-bottom: 35px;
        padding-right: 55px;
      }
      @include from($ipad) {
        font-size: 14px;
      }
      @include from($laptop) {
        font-size: 18px;
      }
      // &:last-child {
      //   @include from($laptop) {
      //     font-size: 16px;
      //   }
      // }
    }
    @include from($vipad) {
      display: flex;
      justify-content: space-between;
    }
  }
  &-checkpoint {
    position: absolute;
    width: 20px;
    height: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    top: 52%;
    right: 33%;
    z-index: 2;
    // &:before {
    //   display: block;
    //   content: '';
    //   position: absolute;
    //   width: 30px;
    //   height: 10px;
    //   top: 15px;
    //   left: -8px;
    //   border: 2px solid rgba(55, 55, 55, 0.6);
    //   border-radius: 50%;
    // }

    @include from($vipad) {
      width: 20px;
      height: 30px;
      top: 18%;
      right: 35%;
    }
    @include from($ipad) {
      width: 15px;
      height: 25px;
      top: 51%;
      right: 47.65%;
    }
    @include from($laptop) {
      width: 27px;
      height: 27px;
      top: 53%;
      right: 47.3%;
    }
    @include from($desktop) {
      width: 36px;
      height: 36px;
      top: 54%;
      right: 47.2%;
    }
  }
}

.map.animated {
  .map-back {
    animation: showFromTop 1.1s 0.25s ease 1 both;
  }
  .map-bottom, .map-wrap{
    animation: showFromBottom 0.5s 0.15s ease 1 both;
  }
  .map-checkpoint {
    animation: pointPulse 0.7s 1s cubic-bezier(0, 0.5, 1, 0.5) 1 both;
  }
}

@keyframes showFromTop {
  from {
    opacity: 0;
    transform: translate(0, -500px);
  }
  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes pointPulse {
  0% {
    opacity: 0;
    transform: translate(0, -20px);
  }
  50% {
    opacity: 1;
    transform: translate(0, 0);
  }
  75% {
    opacity: 1;
    transform: translate(0, -10px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }

}


