.h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;

  @include from($vipad) {
    font-size: 36px;
    line-height: 44px;
  }
  @include from($laptop) {
    font-size: 62px;
    line-height: 70px;
  }
  @include from($desktop) {
    font-size: 80px;
    line-height: 86px;
  }
}

.h2 {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  @include from($vipad) {
    font-size: 24px;
    line-height: 29px;
  }
  @include from($ipad) {
    font-size: 36px;
    line-height: 44px;
  }
  @include from($laptop) {
    font-size: 48px;
    line-height: 94px;
  }
  @include from($desktop) {
    font-size: 64px;
  }
}

.txt {
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  @include from($vipad) {
    font-size: 14px;
    line-height: 38px;
  }
  @include from($laptop) {
    font-size: 18px;
    line-height: 38px;
  }
  &--white {
    color: $c-white;
  }
  &--gray {
    color: $c-gray;
  }

  &-lg {
    font-weight: 500;
    font-size: 14px;
    line-height: 38px;
    @include from($vipad) {
      font-size: 16px;
      line-height: 38px;
    }
    @include from($ipad) {
      font-size: 18px;
    }
    @include from($laptop) {
      font-size: 24px;
      line-height: 38px;
    }
  }
}
