.territory {
  overflow: hidden;
  &.fullheight {
    height: 100vh;
    max-height: 625px;
    @include from($vipad) {
      max-height: none;
      display: flex;
      align-items: center;
    }
    @include from($ipad) {
      // min-height: 670px;
    }
    @include from($laptop) {
      // min-height: 790px;
    }
    @include from($desktop) {
      // min-height: 960px;
    }
  }
  &-back {
    background: url('../img/territory.jpg') no-repeat center center $c-black;
    background-size: cover;
    position: absolute;
    width: 170%;
    height: 100%;
    left: 0;
    top: 0;
    transition: 0.6s ease;

    @include from($vipad) {
      width: 100%;
    }
  }
  .space-size__item {
    width: 56px;
    padding-right: 50px;
    @include from($vipad) {
      width: 70px;
      padding-right: 56px;
    }
    @include from($ipad) {
      padding-right: 80px;
    }
    @include from($laptop) {
      width: 95px;
      padding-right: 100px;
    }
    @include from($desktop) {
      width: 120px;
      padding-right: 130px;
    }

    &:nth-child(1) {
      .space-size__line {
        left: 61px;
        @include from($vipad) {
          left: 63px;
        }
        @include from($ipad) {
          left: 75px;
        }
        @include from($laptop) {
          left: 104px;
        }
        @include from($desktop) {
          left: 135px;
        }
      }
    }
    &:nth-child(2) {
      .space-size__line {
        left: 66px;
        @include from($vipad) {
          left: 80px;
        }
        @include from($laptop) {
          left: 115px;
        }
        @include from($desktop) {
          left: 145px;
        }
      }
    }
  }
  // &-overlay {
  //   position: absolute;
  //   width: 170%;
  //   height: 100%;
  //   left: 0;
  //   top: 0;
  //   background: linear-gradient(
  //     18.68deg,
  //     rgba(0, 0, 0, 0.65) 23.33%,
  //     rgba(0, 0, 0, 0) 74.44%
  //   );
  //   @include from($vipad) {
  //     width: 100%;
  //   }
  // }
  &.show-map {
    .territory-back {
      left: -50%;
      @include from($vipad) {
        left: 0;
      }
    }
    .territory-wrap {
      transform: translate(-50%, 0);
      @include from($vipad) {
        transform: translate(0, 0);
      }
    }
  }
  &-wrap {
    width: 200%;
    position: absolute;
    display: flex;
    flex-flow: row nowrap;
    margin: 85px 0 0;
    transition: 0.6s ease;
    @include from($vipad) {
      margin: 0;
      position: relative;
      width: 100%;
    }
    @include from($ipad) {
      // margin: 115px 0 0;
    }
    @include from($laptop) {
      // margin: 101px 0 0;
    }
    @include from($desktop) {
      // margin: 189px 0 0;
    }

    .territory-info,
    .territory-map {
      width: calc(50% - 54px);
      margin-right: 54px;
      @include from($vipad) {
        width: 50%;
        margin-right: 0;
      }
    }

    .territory-info {
      &__actions {
        display: flex;
        flex-flow: row nowrap;
      }
      .section-title {
        margin-bottom: 30px;
        @include from($vipad) {
          margin-bottom: 35px;
        }
        @include from($ipad) {
          margin-bottom: 42px;
        }
        @include from($laptop) {
          margin-bottom: 26px;
        }
        @include from($desktop) {
          margin-bottom: 47px;
        }

        p {
          @include from($vipad) {
            margin-bottom: 6px;
          }
          @include from($ipad) {
            margin-bottom: 10px;
          }
          @include from($laptop) {
            margin-bottom: 0px;
          }
          @include from($desktop) {
            margin-bottom: 10px;
          }
        }
      }
      &__desc {
        line-height: 24px;
        margin-bottom: 40px;
        @include from($vipad) {
          margin-bottom: 50px;
          font-size: 12px;
        }
        @include from($ipad) {
          font-size: 14px;
          margin-bottom: 61px;
        }
        @include from($laptop) {
          font-size: 18px;
          line-height: 34px;
          margin-bottom: 79px;
        }
        @include from($desktop) {
          margin-bottom: 84px;
        }
      }
      &__actions {
        .btn--outline {
          display: none;
          @include from($vipad) {
            display: block;
          }
          @include from($ipad) {
            padding: 18px 73px;
          }
        }
        .btn--with-icon {
          margin-right: 20px;
        }
      }
    }
    .territory-map {
      position: relative;
      &__dots {
        .dot {
          position: absolute;
          width: 50px;
          height: 50px;
          transform: translate(-50%, -50%);
          background: url('../img/map-dot.svg') no-repeat center center;
          background-size: contain;
          transition: 0.3s ease-out;
          &:hover {
            transform: translate(-50%, -50%) scale(1.1);
          }
        }
      }
      &__background {
        width: 100%;
        max-width: 400px;
        margin: 0 auto;
        margin-top: 58px;
        margin-bottom: 72px;
        svg {
          overflow: visible;
        }
        @include from($vipad) {
          width: 271px;
          margin-top: 35px;
          margin-bottom: 0;
          margin-left: auto;
          max-width: none;
        }

        @include from($ipad) {
          width: 403px;
          margin-top: 47px;
        }
        @include from($laptop) {
          width: 523px;
          margin-top: 67px;
          margin-right: 34px;
        }
        @include from($desktop) {
          width: 682px;
          margin-top: -5px;
          margin-right: 7px;
        }
        svg path,
        svg circle {
          animation: path 5s ease 1;
          @keyframes path {
            0% {
              stroke-dasharray: 0 300;
              stroke-dashoffset: 0;
            }
            100% {
              stroke-dasharray: 300 0;
              stroke-dashoffset: 0;
            }
          }
        }
        #Points {
          g {
            opacity: 0;
            &:hover {
              cursor: pointer;
              circle {
                fill: $c-red;
                r: 25;
                transition: 0.35s;
              }
              polygon {
                fill: $c-white;
              }
            }
          }
        }
      }
    }
  }

  &-popup {
    width: 100%;
    height: 100%;
    overflow: hidden;
    left: 0;
    top: 0;
    position: absolute;
    background: rgba(0, 0, 0, 0.89);
    backdrop-filter: saturate(180%) blur(20px) brightness(200%);
    color: $c-white;
    transform: translate(-100%, 0);
    transition: 1.2s ease;

    @include from($vipad) {
      width: 50%;
      max-width: 960px;
    }

    &__close {
      outline: none;
      cursor: pointer;
      position: absolute;
      right: 20px;
      top: 20px;
      width: 30px;
      height: 30px;
      border-radius: 50px;
      background: $c-white;
      z-index: 2;
      @include from($ipad) {
        right: 30px;
        top: 30px;
      }
      @include from($laptop) {
        right: 60px;
        top: 60px;
        width: 50px;
        height: 50px;
      }
      &:after,
      &:before {
        position: absolute;
        content: '';
        width: 40%;
        height: 1px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        background: $c-black;
      }
      &:before {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
    &__top {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      flex-flow: column wrap;
      justify-content: flex-end;
      transition: 1.2s ease;
      .territory-popup__slider {
        width: 100%;
        flex: 1;
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-end;
        .handle {
          .swiper-slide {
            background-size: cover;
            background-repeat: no-repeat;
          }
        }
        .swiper-pagination-fraction {
          bottom: 129px;
          font-size: 12px;
          letter-spacing: 0.1em;
          @include from($vipad) {
            bottom: 136px;
          }
          @include from($ipad) {
            bottom: 166px;
          }
          @include from($laptop) {
            bottom: 195px;
            font-size: 15px;
          }
        }
        .swiper-button {
          &-next {
            display: block;
            &:after {
              display: none;
            }
            svg {
              width: 100%;
            }
            &:hover {
              svg circle {
                stroke: $c-red;
              }
            }
            width: 30px;
            height: 30px;
            bottom: 121px;
            right: 20px;
            top: auto;
            @include from($vipad) {
              bottom: 129px;
            }
            @include from($ipad) {
              bottom: 159px;
              right: 30px;
            }
            @include from($laptop) {
              bottom: 179px;
              right: 60px;
              width: 50px;
              height: 50px;
            }
          }

          &-prev {
            display: block;
            &:after {
              display: none;
            }
            width: 30px;
            height: 30px;
            bottom: 121px;
            left: 20px;
            top: auto;
            svg {
              width: 100%;
            }
            &:hover {
              svg circle {
                stroke: $c-red;
              }
            }
            @include from($vipad) {
              bottom: 129px;
            }
            @include from($ipad) {
              bottom: 159px;
              left: 30px;
            }
            @include from($laptop) {
              width: 50px;
              height: 50px;
              bottom: 179px;
              left: 60px;
            }
          }
        }
        .actions {
          padding: 20px 20px 0;
          display: flex;
          flex-flow: row nowrap;
          justify-content: space-between;
          align-items: center;
          @include from($ipad) {
            padding: 30px 30px 0;
          }
          @include from($laptop) {
            padding: 50px 60px 0;
          }
          &__prev,
          &__next {
            visibility: hidden;
            outline: none;
            cursor: pointer;
            display: block;
            width: 30px;
            height: 30px;
            border-radius: 50px;
            box-shadow: inset 0 0 0 1px $c-white;
            background: url('../img/right-circle-arrow-white.svg') no-repeat
              center center;
            background-size: contain;
            @include from($laptop) {
              width: 50px;
              height: 50px;
            }
          }
          &__prev {
            transform: rotate(180deg);
          }
          .counter {
            font-size: 12px;
            letter-spacing: 0.1em;
            @include from($laptop) {
              font-size: 15px;
            }
          }
        }
      }
      .text-wrap {
        width: 100%;
        display: flex;
        flex-flow: column nowrap;
        align-items: flex-start;
        padding: 20px 20px 30px;
        @include from($ipad) {
          padding: 30px 30px 45px;
        }
        @include from($laptop) {
          flex-flow: row nowrap;
          align-items: flex-end;
          padding: 50px 60px 80px;
        }
        .territory-popup__more {
          color: $c-white;
          text-align: left;
          white-space: nowrap;
          margin-top: 20px;
        }
      }
    }
    &__bottom {
      position: absolute;
      width: 100%;
      height: 100%;
      transform: translate(0, 100%);
      display: flex;
      flex-flow: column nowrap;
      align-items: flex-start;
      justify-content: flex-start;
      padding: 20px 20px 30px;
      transition: 1.2s ease;
      @include from($ipad) {
        padding: 30px 30px 45px;
      }
      @include from($laptop) {
        padding: 60px 60px 80px;
      }
      .overflow-wrap {
        flex: 1;
        margin: 20px 0;
        overflow: auto;
        @include from($laptop) {
          margin: 40px 0;
        }
      }
      .txt {
        line-height: 1.7;
        margin-bottom: 1rem;
        @include from($ipad) {
          margin-bottom: 1.3rem;
        }
      }
      .btn.visit {
        display: inline-block;
        margin-top: 0.5rem;
      }
    }

    &__title {
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: 0.06em;
      padding-right: 40px;
      word-break: break-word;

      // font-sizes
      font-size: 18px;
      line-height: 1.3;
      @include from($vipad) {
        font-size: 24px;
      }
      @include from($ipad) {
        font-size: 28px;
      }
      @include from($laptop) {
        font-size: 32px;
        padding-right: 50px;
        margin-bottom: 0;
      }
      @include from($desktop) {
        font-size: 36px;
      }
    }

    &.show {
      transform: translate(0, 0);
    }

    &.show-more {
      .territory-popup__close {
        background: transparent;
        box-shadow: inset 0 0 0 1px $c-white;
        &:after,
        &:before {
          background: $c-white;
        }
      }
      .territory-popup__top {
        transform: translate(0, -100%);
      }
      .territory-popup__bottom {
        transform: translate(0, 0);
      }
    }
  }
}
