.for-mobile {
	@include from($vipad) {
    visibility: hidden;
    display: none;
	}
}

.not-for-mobile {
  visibility: hidden;
	@include from($vipad) {
		visibility: visible;
	}
}

.vertical-center {
  display: flex;
  align-items: center;
}

