.partners {
  display: none;
  
  &-wrap {
    .section-title {
      margin-top: 67px;
      margin-bottom: 32px;
      @include from($vipad) {
        margin-top: 85px;
        margin-bottom: 40px;
      }
      @include from($ipad) {
        margin-top: 74px;
        margin-bottom: 66px;
      }
      @include from($laptop) {
        margin-top: 128px;
      }
      @include from($desktop) {
        margin-top: 157px;
        margin-bottom: 90px;
      }
      .h2 {
        text-align: center;
      }
    }
    .partners-grid {
      display: flex;
      flex-flow: row wrap;
      width: 270px;
      margin: 0 auto;
      justify-content: space-between;
      @include from($vipad) {
        width: 640px;
      }
      @include from($ipad) {
        width: 853px;
      }
      @include from($laptop) {
        width: 1200px;
      }
      @include from($desktop) {
        width: 1280px;
      }

      .partners__item {
        width: 125px;
        height: 62px;
        background-size: cover;
        background-repeat: no-repeat;
        margin-bottom: 20px;

        @include from($vipad) {
          width: 200px;
          height: 100px;
        }
        @include from($ipad) {
          width: 265px;
          height: 131px;
          margin-bottom: 30px;
        }
        @include from($laptop) {
          width: 375px;
          height: 186px;
          margin-bottom: 35px;
        }
        @include from($desktop) {
          width: 398px;
          height: 200px;
          margin-bottom: 40px;
        }
      }
    }
  }
}

.partners-animation.animated {
  .section-title {
    animation: showFromBottom 0.6s 0.25s ease 1 both;
  }
  .partners__item {
    &:nth-child(1) {
      animation: showFromBottom 0.5s 0.4s ease 1 both;
    }
    &:nth-child(2) {
      animation: showFromBottom 0.5s 0.6s ease 1 both;
    }
    &:nth-child(3) {
      animation: showFromBottom 0.5s 0.8s ease 1 both;
    }
    &:nth-child(4) {
      animation: showFromBottom 0.5s 1s ease 1 both;
    }
    &:nth-child(5) {
      animation: showFromBottom 0.5s 1.2s ease 1 both;
    }
    &:nth-child(6) {
      animation: showFromBottom 0.5s 1.4s ease 1 both;
    }
  }
}

.info-partners-animation.animated {
  .section-title {
    animation: showFromBottom 0.6s 0.25s ease 1 both;
  }
  .partners__item {
    &:nth-child(1) {
      animation: showFromBottom 0.5s 0.4s ease 1 both;
    }
    &:nth-child(2) {
      animation: showFromBottom 0.5s 0.6s ease 1 both;
    }
    &:nth-child(3) {
      animation: showFromBottom 0.5s 0.8s ease 1 both;
    }
    &:nth-child(4) {
      animation: showFromBottom 0.5s 1s ease 1 both;
    }
    &:nth-child(5) {
      animation: showFromBottom 0.5s 1.2s ease 1 both;
    }
    &:nth-child(6) {
      animation: showFromBottom 0.5s 1.4s ease 1 both;
    }
  }
}

.partners-wrap.animated, .video.animated, .animated .grid-container ,.partners-animation {
  opacity: 1;
  animation: none;
}

.partners-wrap, .video, .grid-container {
  opacity: 0;
  animation: hideBlock 0.5s ease 1 both;
}


@keyframes hideBlock {
  from {
    opacity: 1;
    animation: none;
  }
  to {
    opacity: 0;
  }
}

.footer {
  .grid-container {
    opacity: 1;
    animation: none;
  }
}
